import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders} from '@angular/common/http';
import { Invoice } from '../admin/invoice/invoice';
import { endpoint } from 'src/environments/environment.prod';

@Injectable({
  providedIn: 'root'
})
export class InvoiceSerService {
  public url=endpoint.url;
  private invoice_url:string=this.url+"invoice/";
  private invoice_url1:string=+this.url+"invoice1/";
  private monthlypayment:string=this.url+"monthlypayment/";
  private highestselling:string=this.url+"highestsellingitem/";
  constructor(private _http:HttpClient) { }

  getAllInvoice(){
    return this._http.get(this.invoice_url);
  }
  getInvoiceByTable(Tableno){
    return this._http.get(this.invoice_url+Tableno);
  }
  addInvoice(item,mode){
    let body=JSON.stringify(item);
    let head1=new HttpHeaders().set('Content-Type','application/json');
    return this._http.post(this.invoice_url+mode,body,{headers:head1});
  }
  getInvoiceByMode(PaymentMODE)
  {
    return this._http.get(this.invoice_url1+PaymentMODE);
  }
  getMonthlyPayment()
  {
    return this._http.get(this.monthlypayment);
  }
  getHighestSellingItem()
  {
    return this._http.get(this.highestselling);
  }
}
