import { Component, OnInit } from '@angular/core';
import { EmployeeSerService } from 'src/app/services/employee-ser.service';
import { Employee } from '../employee/employee';
import { Router } from '@angular/router';

@Component({
  selector: 'app-change-password',
  templateUrl: './change-password.component.html',
  styleUrls: ['./change-password.component.css']
})
export class ChangePasswordComponent implements OnInit {
  EmpID:string;
  EmpPassword:string;
  EmpRepassword:string;
  flag:boolean=true;
  flag1:boolean=true;
  encryptPassword:string="";
  constructor(private _Ser:EmployeeSerService,private _route:Router) { }

  ngOnInit() {
    this.EmpID = localStorage.getItem('empid');

  }
  encrypt(item:string){

    for(let i=0;i<item.length;i++)
    {
      if(i%2==0){
        this.encryptPassword+="@#"+item[i];
      }
      else{
        this.encryptPassword+=item[i];
      }
    }
    console.log(this.encryptPassword);
  }
  onSave(){

        if(this.EmpPassword==this.EmpRepassword)
        {
          this.encrypt(this.EmpPassword);
          this._Ser.changepass(new Employee(this.EmpID,this.encryptPassword)).subscribe(
            (data:any)=>{
          alert('Password is Changed , You have to Re Login');
          this._route.navigate(['loginpage']);
            }
          )
        }
        else{
          alert('Password must be same')
        }

  }
  onBack(){
    this._route.navigate(['menu']);
  }

}
