import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Item } from 'src/app/admin/item/item';
import { ItemSerService } from 'src/app/services/item-ser.service';
import { StockSerService } from 'src/app/services/stock-ser.service';
import { MatTableDataSource } from '@angular/material';

@Component({
  selector: 'app-view-dish-detail',
  templateUrl: './view-dish-detail.component.html',
  styleUrls: ['./view-dish-detail.component.css']
})
export class ViewDishDetailComponent implements OnInit {
itemid:number;
itemarr:Item[]=[];
itemname:string="";
itemprice:number=0;
itemtype:string="";
itemtime:string="";
itemcat:string="";
itemimg:string="";
itemingridents:string[];
str:string="";
stock=new MatTableDataSource();
stockColumns:string[]=['Name','QTY'];
qty:string[]=[];
  constructor(private _receive:ActivatedRoute,private _item:ItemSerService,private _stock:StockSerService,private _send:Router) { }

  ngOnInit() {
    this.itemid=this._receive.snapshot.params['itemID'];
    console.log(this.itemid);
    this._item.getItemById(this.itemid).subscribe(
      (data:Item[])=>{
        this.itemarr=data;
        console.log(this.itemarr);
        this.itemname=this.itemarr[0].itemNAME;
        this.itemprice=this.itemarr[0].itemPRICE;
        this.itemtype=this.itemarr[0].itemTYPE;
        this.itemtime=this.itemarr[0].itemPREPARETIME;
        this.itemimg=this.itemarr[0].itemIMG;
        this.str=this.itemarr[0].itemINGREDIENTS+"0";
        this.itemingridents=this.str.split('|');
        console.log(this.itemingridents);
        let id=[];

        for(let i=0;i<this.itemingridents.length;i++){
            if(i%2==0){
              id.push(this.itemingridents[i]);
            }
            else{
              this.qty.push(this.itemingridents[i]);
            }
        }
        console.log(this.qty);
        this._stock.getStockByIn(id).subscribe(
          (data:any)=>{
            console.log(data);
            this.stock.data=data;
          }
        );

      }
    );
  }
  onClickBack(){
    this._send.navigate(['captain']);
  }
}
