import { Component,OnInit ,ViewChild,OnDestroy} from '@angular/core';
import { map, timeInterval } from 'rxjs/operators';
import { MatPaginator, MatSort, MatTableDataSource, MatDialogRef, MatDialog,MAT_DIALOG_DATA } from "@angular/material";
import {  BreakpointObserver } from '@angular/cdk/layout';
import { OrderItem } from 'src/app/cashier/orderitem';
import { OrderserviceService } from 'src/app/services/orderservice.service';
import { Router } from '@angular/router';
import { Charity } from '../charity-admin/charity';
import { CharityserviceService } from 'src/app/services/charityservice.service';
import { EmployeeSerService } from 'src/app/services/employee-ser.service';
import { Sendemail } from 'src/app/forgetpassword/sendemail';
import { StockSerService } from 'src/app/services/stock-ser.service';
import { Stock } from '../stock/stock';
import { InvoiceSerService } from 'src/app/services/invoice-ser.service';

import { Chart } from "Chart.js";

import { ItemSerService } from 'src/app/services/item-ser.service';
import { CategorySerService } from 'src/app/services/category-ser.service';
import { Category } from '../category/category';
import { timer } from 'rxjs';

@Component({
  selector: 'app-admin-home',
  templateUrl: './admin-home.component.html',
  styleUrls: ['./admin-home.component.css']
})


export class AdminHomeComponent {
  /** Based on the screen size, switch from standard to one column per row */

  dataSource = new MatTableDataSource();
  @ViewChild(MatPaginator)
  paginator: MatPaginator;
  paginator1: MatPaginator;
  paginator2: MatPaginator;
  @ViewChild(MatSort)
  sort: MatSort;
  sort1: MatSort;
  sort2:MatSort;
  charity:Charity[]=[];
  order:OrderItem[]=[];
  stockarr:Stock[]=[];
  totalcash:number=0;
  totalcard:number=0;
  totalother:number=0;
  paymentchartflag:boolean;
  minDate = new Date(2000, 0, 1);
  maxDate = new Date(2020, 0, 1);
  catarr:Category[]=[];
  displayedColumns: string[] = ["item","qty","price","tableNo"];
  dataSource1=new MatTableDataSource();
  highestselling=new MatTableDataSource();
  displayedColumns1:string[]=['StockNAME','StockQTY','StockALERT'];
  highestSellingColumns:string[]=['ItemNAME','QTY'];
  date1:Date=new Date();
  sellingpie=[];
  monthlyselling=[];
  monthlypayment=[];
  timeinterval:any;
  todaypaytimer:any;
  monthlysellingtimer:any;
  highestsellingtimer:any;
  monthlypaymenttimer:any;
  flag:boolean=true;
  constructor(private _route:Router,private breakpointObserver: BreakpointObserver,private _ser:OrderserviceService,private _ser1:EmployeeSerService,private _charityser:CharityserviceService,private _stock:StockSerService,private _invser:InvoiceSerService,private _item:ItemSerService,private _cat:CategorySerService) {}
  onclick(){
    this._route.navigate(['menu/order']);
  }
  ngOnInit()
  {
    console.log(this.date1.toDateString());
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
    this.dataSource1.paginator = this.paginator1;
    this.dataSource1.sort = this.sort1;
    //this.highestselling.paginator = this.paginator;
    this.highestselling.sort = this.sort;
    this._cat.getAllCategory().subscribe(
      (data:Category[])=>{
        this.catarr=data;
      }
    );
    this.getAllOrder();
    this.oncharity();
    this.onclickstock();
    this.represhdata();
  }
  ngOnDestroy(){
    if(this.todaypaytimer)
    {
      this.todaypaytimer.unsubscribe();
    }
    if(this.monthlypaymenttimer){
      this.monthlypaymenttimer.unsubscribe();
    }
    if(this.monthlysellingtimer){
      this.monthlysellingtimer.unsubscribe();
    }
    if(this.highestsellingtimer){
      this.highestsellingtimer.unsubscribe();
    }
    this.flag==false;
  }
  subscriptiondata(){
    if(this.flag){
      this.timeinterval=timer(10000).subscribe(()=>this.represhdata());
    }

  }
  private represhdata(): void {
    console.log("helllo");
    this.onTodayPay();
    this.onmonthlyselling();
      this.onMonthlyPayment();
      this.onHighestSellingProduct()
    //  this.subscriptiondata();
  }
  getAllOrder()
  {
    this._ser.getOrderByStatus("process").subscribe(
      (data:any)=>{
        this.order=data;
        this.dataSource.data=this.order;
      }
    )
  }
  onclickstock(){
    this._stock.getAlertstock().subscribe(
      (data:Stock[])=>{
        this.stockarr=data;
        this.dataSource1.data=this.stockarr;
      }
    );
  }
  oncharity()
  {
    this._charityser.getAllCharity().subscribe(
      (data:any)=>{
        this.charity=data;
      }
    )
  }
  onsend(item){
    console.log(item);
    this._ser1.sendMail(new Sendemail(item,"ANTICA PIZZERIA","Collect the leftover Food")).subscribe(
      (data:any)=>{
      alert("Mail has been Sent");
      }
    )
  }
  onTodayPay()
  {
    this.todaypaytimer=this._invser.getInvoiceByMode("CARD").subscribe(
      (data:any)=>{
        //console.log(data);
        if(data[0].total){
          this.totalcard=data[0].total;
        }
        else{
          this.totalcard=0;
        }
       console.log(this.totalcard);
      }
    );
    this._invser.getInvoiceByMode("OTHER").subscribe(
      (data:any)=>{
        //console.log(data);
        if(data[0].total){
          this.totalother=data[0].total;
        }
        else{
          this.totalother=0;
        }
       console.log(this.totalcard);
      }
    );
    this._invser.getInvoiceByMode("CASH").subscribe(
      (data:any)=>{
        console.log(data);
        if(data[0].total){
          this.totalcash=data[0].total;
        }
        else{
          this.totalcash=0;
        }
        console.log(this.totalcash);
        if(this.totalcard==0 && this.totalcash==0 && this.totalother==0){
          console.log("hello");
          this.paymentchartflag=true;
        }
        else{
          this.paymentchartflag=false;
        }
        this.sellingpie=new Chart('canvas1',{
          type:'pie',
          data:{
            labels:[
              'CARD',
              'CASH',
              'OTHER'
            ],
            datasets:[
              {
              data:[this.totalcard,this.totalcash,this.totalother],
              backgroundColor:['rgba(0,200,0,100)','rgba(255,90,0,40)','rgba(0,0,255,50)']
            },

          ]
          },
          options:{
            reponsive:true
          }
        });
      }
    );
  }
  onHighestSellingProduct(){
    this.highestsellingtimer=this._invser.getHighestSellingItem().subscribe(
      (data:any)=>{
        let arr=[];
        arr=data;
        this.highestselling.data=arr;
      }
    );
  }
  onMonthlyPayment(){
    this.monthlypaymenttimer=this._invser.getMonthlyPayment().subscribe(
      (data:any)=>{
        console.log(data);
        let mode=[];
        let payment=[];
        for(let i=0;i<data.length;i++){
          mode.push(data[i].PaymentMODE);
          payment.push(data[i].total);
        }
        this.monthlypayment=new Chart('canvas3',{
          type:'bar',
          data:{
            labels:
              mode
            ,
            datasets:[
              {
                label:'Monthly Payments',
              data:payment,
              backgroundColor:'rgba(200,0,150,100)',
              borderWidth:1,
            },


          ]
          },
          options:{
            reponsive:true,
            scales:{
              yAxes:[{
                  ticks:{
                    beginAtZero:true
                  }
              }],
              xAxes:[{
                display:true,
                scaleLabel:{
                  display:true,
                  labelString:'Payment Mode'
                }
              }]
            }

          }
      });
      }
    );
  }
onmonthlyselling()
{
    this.monthlysellingtimer=this._item.MonthlySellingCategoryWise().subscribe(
      (data:any) => {
        //console.log(data);
        var name=[];
        var cnt=[];
        let flag=0
        for(let i=0;i<this.catarr.length;i++){
          flag=0;
         for(let j=0;j<data.length;j++){
           if(this.catarr[i].CategoryNAME==data[j].CategoryNAME){

            name.push(this.catarr[i].CategoryNAME);
            cnt.push(data[j].count);
             flag=1;
             break
           }
         }
         if(flag==0){
          name.push(this.catarr[i].CategoryNAME);
          cnt.push(0);
         }

        }
        //console.log(cnt);
        this.monthlyselling=new Chart('canvas2',{
            type:'bar',
            data:{
              labels:
                name
              ,
              datasets:[
                {
                  label:'Monthly sold items',
                data:cnt,
                backgroundColor:'rgba(200,200,0,40)',
                borderWidth:1
              },


            ]
            },
            options:{
              reponsive:true,
              scales:{
                yAxes:[{
                    ticks:{
                      beginAtZero:true
                    }
                }],
                xAxes:[{
                  display:true,
                  scaleLabel:{
                    display:true,
                    labelString:'Category Name'
                  }
                }]
              }

            }
        });

      });
}
  onMonPay()
  {

  }
  onYerPay()
  {

  }
  applyFilter(filterValue: string) {
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }
}
