import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders} from '@angular/common/http';
import { endpoint } from 'src/environments/environment.prod';

@Injectable({
  providedIn: 'root'
})
export class InvoicedetailSerService {
  public url=endpoint.url;
  private invoicedetail_url:string=this.url+"invoicedetail/";
  constructor(private _http:HttpClient) { }
  getInvoiceDetailById(id){
    return this._http.get(this.invoicedetail_url+id);
  }
}
